import FilterDialogActionTypes from '../constants/FilterDialogActionTypes';
import DataActionTypes from '../../Map/constants/DataActionTypes';
import DefaultFilters from 'DefaultFilters';

const defaultState = {
  open: false,
  filters: DefaultFilters.createDefaultFilters(),
  timeframeMenu: {
    open: false,
    mode: 'from',
    anchor: null,
  }
};

const filterDialogReducer = (state = defaultState, action) => {

  switch (action.type) {
  case FilterDialogActionTypes.OPEN:
    return {
      ...state,
      open: true,
    };
  case FilterDialogActionTypes.CLOSE:
    return {
      ...state,
      open: false,
    };
  case FilterDialogActionTypes.UPDATE_FILTERS:
    return {
      ...state,
      filters: {...action.filters},
    };
  case FilterDialogActionTypes.UPDATE_TIMEFRAME_MENU:
    return {
      ...state,
      timeframeMenu: {...action.timeFrameMenu},
    };
  case DataActionTypes.SAVE_FILTERS:
    return {
      ...state,
      filters: {...action.filters},
    };
  default:
    return state;
  }
};

export default filterDialogReducer;


