import DataActionTypes from '../constants/DataActionTypes';
import DefaultFilters from 'DefaultFilters';

const defaultState = {
  loading: false,
  images: [],
  filters: DefaultFilters.createDefaultFilters(),
  filteredImages: [],
};

const filterImages = (images, filters) => {
    const { viewpoints, timeframe } = filters;

  return images.filter((image) => {

    const viewpointVisible = viewpoints[image.type].checked;
    const inTimeFrame = timeframe.start <= image.year
      && image.year <= timeframe.end;

    return viewpointVisible && inTimeFrame;
  });
};

const dataReducer = (state = defaultState, action) => {
  switch (action.type) {
  case DataActionTypes.LOAD:
    return {
      ...state,
      loading: true,
    };
  case DataActionTypes.LOAD_SUCCESS:
    return {
      ...state,
      loading: false,
      images: action.images,
      filteredImages: filterImages(action.images, state.filters),
    };
  case DataActionTypes.SAVE_FILTERS:
    const filters = action.filters;
    return {
      ...state,
      filters,
      filteredImages: filterImages(state.images, filters),
    };
  default:
    return state;
  }
};

export default dataReducer;


