import ClusterIcon from './ClusterIcon';

export default class Cluster {

  constructor(markerClusterer) {
    this.markerClusterer = markerClusterer;
    this.map = markerClusterer.map;
    this.gridSize = markerClusterer.gridSize;
    this.minClusterSize = markerClusterer.minClusterSize;
    this.center = null;
    this.markers = [];
    this.bounds = null;
    this.clusterIcon = new ClusterIcon(this);
  }

  isMarkerAlreadyAdded(marker) {
    return this.markers.indexOf(marker) !== -1;
  }

  addMarker(marker) {
    if (this.isMarkerAlreadyAdded(marker)) {
      return false;
    }

    if (!this.center) {
      this.center = marker.getPosition();
      this.calculateBounds();
    }

    marker.isAdded = true;
    this.markers.push(marker);

    const len = this.markers.length;
    if (len < this.minClusterSize && marker.getMap() !== this.map) {
      // Min cluster size not reached so show the marker.
      marker.setMap(this.map);
    }

    if (len === this.minClusterSize) {
      // Hide the markers that were showing.
      for (let i = 0; i < len; i++) {
        this.markers[i].setMap(null);
      }
    }

    if (len >= this.minClusterSize) {
      marker.setMap(null);
    }

    this.updateIcon();
    return true;
  }

  getBounds() {
    const bounds = new window.google.maps.LatLngBounds(this.center,
      this.center);
    this.markers.forEach((marker) => {
      bounds.extend(marker.getPosition());
    });
    return bounds;
  };

  remove() {
    this.clusterIcon.remove();
    this.markers = [];
  }

  calculateBounds() {
    const bounds = new window.google.maps.LatLngBounds(this.center, this.center);
    this.bounds = this.markerClusterer.getExtendedBounds(bounds);
  };

  isMarkerInClusterBounds(marker) {
    return this.bounds.contains(marker.getPosition());
  }

  updateIcon() {
    const zoom = this.map.getZoom();
    if (zoom > 18) {
      this.markers.forEach((marker) => marker.setMap(this.map));
      return;
    }

    if (this.markers.length < this.minClusterSize) {
      // Min cluster size not yet reached.
      this.clusterIcon.hide();
      return;
    }

    this.clusterIcon.center = this.center;
    this.clusterIcon.setSums(this.markers.length);
    this.clusterIcon.show();
  };
}


