import { connect } from 'react-redux';
import Viewer from './Viewer';
import ViewerActions from './actions/ViewerActions';

const mapStateToProps = (state) => {
  return state.viewer;
};

const mapDispatchToProps = (dispatch) => ({
  onClose: () => ViewerActions.close()(dispatch),
  onLoadImage: (imageId) => ViewerActions.loadImage(imageId)(dispatch)
});

const ConnectedViewer = connect(
  mapStateToProps,
  mapDispatchToProps
)(Viewer);

export default ConnectedViewer;


