import { connect } from 'react-redux';
import FilterDialog from './FilterDialog';
import FilterDialogActions from '../actions/FilterDialogActions';
import DataActions from '../../Map/actions/DataActions';

const mapStateToProps = (state) => {
  return state.filterDialog;
};

const mapDispatchToProps = (dispatch) => ({
  onCloseFilterDialog: () => FilterDialogActions.close()(dispatch),
  onUpdateFilters: (filters) => FilterDialogActions.updateFilters(filters)(dispatch),
  onUpdateTimeframeMenu: (timeframeMenu) => FilterDialogActions.updateTimeframeMenu(timeframeMenu)(dispatch),
  onSaveFilters: (filters) => DataActions.saveFilters(filters)(dispatch),
});

const ConnectedFilterDialog = connect(
  mapStateToProps,
  mapDispatchToProps
)(FilterDialog);


export default ConnectedFilterDialog;


