import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Button from 'material-ui/Button';
import Dialog, {
  DialogActions,
  DialogContent,
  DialogTitle,
} from 'material-ui/Dialog';
import {
  FormGroup,
  FormControlLabel
} from 'material-ui/Form';
import Menu, { MenuItem } from 'material-ui/Menu';
import Checkbox from 'material-ui/Checkbox';
import Visibility from 'material-ui-icons/Visibility';
import VisibilityOff from 'material-ui-icons/VisibilityOff';
import ArrowDropDown from 'material-ui-icons/ArrowDropDown';
import Typography from 'material-ui/Typography';

import styles from './FilterDialog.css';

class FilterDialog extends Component {

  static propTypes = {
    open: PropTypes.bool.isRequired,
    filters: PropTypes.object.isRequired,
    timeframeMenu: PropTypes.object.isRequired,
    onCloseFilterDialog: PropTypes.func.isRequired,
    onUpdateFilters: PropTypes.func.isRequired,
    onSaveFilters: PropTypes.func.isRequired,
    onUpdateTimeframeMenu: PropTypes.func.isRequired,
  };

  handleRequestClose = () => {
    this.props.onCloseFilterDialog();
  }

  handleRequestCloseAndFilter = () => {
    this.props.onCloseFilterDialog();
    this.props.onSaveFilters(this.props.filters);
  }

  handleOpenTimeFrameMenu = (mode) => (e) => {
    this.props.onUpdateTimeframeMenu({
      open: true,
      mode: mode,
      anchor: e.currentTarget,
    });
  }

  handleYearSelected = (year) => () => {
    const { mode } = this.props.timeframeMenu;

    const { ...filters } = this.props.filters;
    const { ...timeframe } = filters.timeframe;

    if (mode === 'from') {
      timeframe.start = year;
    } else {
      timeframe.end = year;
    }

    filters.timeframe = timeframe;
    this.props.onUpdateFilters(filters);
  }

  handleCloseTimeframeMenu = () => {
    this.props.onUpdateTimeframeMenu({
      open: false,
      mode: 'from',
      anchor: null,
    });
  }

  handleViewpointChange = (viewpointKey) => (e) => {
    const { ...filters } = this.props.filters;
    const { ...viewpoints } = filters.viewpoints;
    const { ...viewpoint } = viewpoints[viewpointKey];
    viewpoint.checked = e.target.checked;
    viewpoints[viewpointKey] = viewpoint;
    filters.viewpoints = viewpoints;
    this.props.onUpdateFilters(filters);
  };

  renderCheckboxes() {

    const { viewpoints } = this.props.filters;

    return Object.keys(viewpoints).map((viewpointKey) => {

      const viewpoint = viewpoints[viewpointKey];

      return (
        <FormControlLabel
          key={viewpointKey}
          control={
            <Checkbox
              checked={viewpoint.checked}
              onChange={this.handleViewpointChange(viewpointKey)}
              value={viewpoint.label}
              checkedIcon={<Visibility />}
              icon={<VisibilityOff />}
            />
          }
          label={viewpoint.label}
        />
      );
    });
  }

  renderTimeframeButtons() {
    const { timeframe } = this.props.filters;

    return (
      <div >
        From:
        <Button onClick={this.handleOpenTimeFrameMenu('from')}>
          {timeframe.start} <ArrowDropDown />
        </Button>
        To:
        <Button onClick={this.handleOpenTimeFrameMenu('to')}>
          {timeframe.end} <ArrowDropDown />
        </Button>
        {this.renderTimeframeMenu()}
      </div>
    );
  }

  renderTimeframeMenu() {

    const { open, mode, anchor } = this.props.timeframeMenu;
    const { timeframe } = this.props.filters;

    let min = 0;
    let max = Number.MAX_VALUE;
    if (mode === 'from') {
      min = timeframe.min;
      max = timeframe.end;
    } else {
      min = timeframe.start;
      max = timeframe.max;
    }

    const years = [];
    for (let year = min; year <= max; year++) {
      years.push(year);
    }

    return (
      <Menu
        anchorEl={anchor}
        open={open}
        onRequestClose={this.handleCloseTimeframeMenu}
        onClick={this.handleCloseTimeframeMenu}
        style={{ maxHeight: 500 }}
      >
        {years.map((year) =>
          <MenuItem
            key={year}
            onClick={this.handleYearSelected(year)}
          >
            {year}
          </MenuItem>,
        )}
      </Menu>
    );
  }

  render() {

    return (
      <Dialog open={this.props.open} onRequestClose={this.handleRequestClose}>
        <DialogTitle>
          Filter Photos
        </DialogTitle>
        <DialogContent>
          <Typography type="subheading">
            View Points
          </Typography>
          <FormGroup row>
            {this.renderCheckboxes()}
          </FormGroup>
          <div className={styles.spacing} ></div>
          <Typography type="subheading">
            Time Frame
          </Typography>
          {this.renderTimeframeButtons()}
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleRequestClose} color="primary">
            Cancel
          </Button>
          <Button onClick={this.handleRequestCloseAndFilter} color="primary">
            Filter
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}

export default FilterDialog;


