import loadjs from 'loadjs';

const load = () => {
  return new Promise((resolve, reject) => {
    loadjs('https://maps.googleapis.com/maps/api/js?key=AIzaSyDmnNByRIs_bN9YgvclWpMCv2-OfO2RPIM', {
      success: () => {
        resolve();
      },
      error: () => {
        reject();
      }
    });
  });
};

export default { load };


