import { getOverlayClass } from './OverlayViewProvider';

const CLUSTER_ICON = 'https://static1.squarespace.com/static/54c92a1de4b01578e1ac210f/t/59791619a803bb2138306d3f/1501107737395/checkbox-multiple-blank-outline.png';
const CLUSTER_ICON_SIZE = 36;

export default class ClusterIcon {

  constructor(cluster) {
    this.cluster = cluster;
    this.center = null;
    this.map = cluster.map;
    this.div = null;
    this.sums = null;
    this.visible = false;
    this.overlay = new (getOverlayClass())(this.map, this);
  }

  triggerClusterClick() {
    const markerClusterer = this.cluster.markerClusterer;

    // Trigger the clusterclick event.
    window.google.maps.event.trigger(markerClusterer, 'clusterclick',
      this.cluster);

    // Zoom into the cluster.
    this.map.fitBounds(this.cluster.getBounds());
  }

  onAdd() {
    this.div = document.createElement('div');
    if (this.visible) {
      var pos = this.getPosFromLatLng(this.center);
      this.div.style.cssText = this.createCss(pos);
      this.div.innerHTML = this.getLabel();
    }

    const panes = this.overlay.getPanes();
    panes.overlayMouseTarget.appendChild(this.div);

    window.google.maps.event.addDomListener(this.div, 'click', () => {
      this.triggerClusterClick();
    });
  }

  getPosFromLatLng(latlng) {
    const pos = this.overlay.getProjection().fromLatLngToDivPixel(latlng);
    pos.x -= parseInt(CLUSTER_ICON_SIZE / 2, 10);
    pos.y -= parseInt(CLUSTER_ICON_SIZE / 2, 10);
    return pos;
  }

  draw() {
    if (this.visible) {
      var pos = this.getPosFromLatLng(this.center);
      this.div.style.top = pos.y + 'px';
      this.div.style.left = pos.x + 'px';
    }
  }

  hide() {
    if (this.div) {
      this.div.style.display = 'none';
    }
    this.visible = false;
  }

  show() {
    if (this.div) {
      const pos = this.getPosFromLatLng(this.center);
      this.div.style.cssText = this.createCss(pos);
      this.div.style.display = '';
    }
    this.visible = true;
  }

  remove() {
    this.overlay.setMap(null);
  }

  onRemove() {
    if (this.div && this.div.parentNode) {
      this.hide();
      this.div.parentNode.removeChild(this.div);
      this.div = null;
    }
  }

  setSums(sums) {
    this.sums = sums;
    if (this.div) {
      this.div.innerHTML = this.getLabel();
    }
  };

  getLabel() {
    return this.sums > 9 ? '9+' : this.sums;
  }

  createCss(pos) {
    const style = [];
    style.push('background-image:url(' + CLUSTER_ICON + ');');
    style.push('background-position: 0 0;');

    style.push('height:' + CLUSTER_ICON_SIZE + 'px; line-height:'
      + 25 + 'px; width:' + CLUSTER_ICON_SIZE +
      'px; text-align:center;');

    const txtColor = 'black';
    const txtSize = 14;

    style.push('cursor:pointer; top:' + pos.y + 'px; left:' +
      pos.x + 'px; color:' + txtColor + '; position:absolute; font-size:' +
      txtSize + 'px; font-family:"Roboto", "Helvetica", "Arial", sans-serif; font-weight:bold;' +
      'box-sizing: border-box;' +
      'padding: 3px 3px 9px 9px;' +
      'text-align: center;'
    );
    return style.join('');
  }
}


