import FilterDialogActionTypes from '../constants/FilterDialogActionTypes';

const open = () => ({
  type: FilterDialogActionTypes.OPEN,
});

const close = () => ({
  type: FilterDialogActionTypes.CLOSE,
});

const updateFilters = (filters) => ({
  type: FilterDialogActionTypes.UPDATE_FILTERS,
  filters,
});

const updateTimeframeMenu = (timeFrameMenu) => ({
  type: FilterDialogActionTypes.UPDATE_TIMEFRAME_MENU,
  timeFrameMenu,
});

export default {
  open: () => (dispatch) => dispatch(open()),
  close: () => (dispatch) => dispatch(close()),
  updateFilters: (filters) => (dispatch) => dispatch(updateFilters(filters)),
  updateTimeframeMenu: (timeframeMenu) => (dispatch) => dispatch(updateTimeframeMenu(timeframeMenu)),
};


