let OverlayClass = null;

export const getOverlayClass = () => {
  if (OverlayClass) {
    return OverlayClass;
  }
  const create = () => {
    function Overlay(map, delegate) {
      this.delegate = delegate;
      this.setMap(map);
    }

    Overlay.prototype = new window.google.maps.OverlayView();

    Overlay.prototype.onAdd = function () {
      this.delegate.onAdd()
    };

    Overlay.prototype.draw = function () {
      this.delegate.draw && this.delegate.draw()
    };

    Overlay.prototype.onRemove = function () {
      this.delegate.onRemove && this.delegate.onRemove()
    };

    return Overlay;
  };

  OverlayClass = create();
  return OverlayClass;
};


