import { connect } from 'react-redux';
import Map from './Map';
import MapActions from './actions/MapActions';
import DataActions from './actions/DataActions';
import ViewerActions from '../Viewer/actions/ViewerActions';

const mapStateToProps = (state) => {
  const {loading: loadingMap, map} = state.map;
  const {loading: loadingData, filteredImages} = state.data;
  return {
    loadingMap,
    loadingData,
    map,
    images: filteredImages,
  };
};

const mapDispatchToProps = (dispatch) => ({
  onLoadMap: (mapId) => MapActions.load(mapId)(dispatch),
  onLoadData: () => DataActions.load()(dispatch),
  onOpenImage: (imageId) => ViewerActions.open(imageId)(dispatch)
});

const ConnectedMap = connect(
  mapStateToProps,
  mapDispatchToProps
)(Map);

export default ConnectedMap;


