import ViewerActionTypes from '../constants/ViewerActionTypes';

const defaultState = {
  open: false,
  loading: false,
  photoInfo: null,
  image: null,
};

const viewerReducer = (state = defaultState, action) => {

  switch (action.type) {
  case ViewerActionTypes.OPEN:
    return {
      ...state,
      open: true,
      photoInfo: action.photoInfo,
    };
  case ViewerActionTypes.CLOSE:
    return {
      ...state,
      open: false,
      photoInfo: null,
      image: null,
    };
  case ViewerActionTypes.LOAD_IMAGE:
    return {
      ...state,
      loading: true,
    };
  case ViewerActionTypes.LOAD_IMAGE_SUCCESS:
    return {
      ...state,
      loading: false,
      image: action.image
    };
  default:
    return state;
  }
};

export default viewerReducer;


