import DataActionTypes from '../constants/DataActionTypes';
import DataApi from '../api/DataApi';

const loadStart = () => ({
  type: DataActionTypes.LOAD,
});

const loadSuccess = (images) => ({
  type: DataActionTypes.LOAD_SUCCESS,
  images: images,
});

const saveFilters = (filters) => ({
  type: DataActionTypes.SAVE_FILTERS,
  filters,
});

const load = () => (dispatch) => {
  dispatch(loadStart());

  return DataApi.load('HARLEM')
    .then((images) => {
      dispatch(loadSuccess(images));
    });
};

export default {
  load,
  saveFilters: (filters) => (dispatch) => dispatch(saveFilters(filters)),
};


