import MapActionTypes from '../constants/MapActionTypes';
import GMapsApi from '../api/GMapsApi';
import MapStyles from '../constants/MapStyles';

const loadStart = () => ({
  type: MapActionTypes.LOAD,
});

const loadSuccess = (map) => ({
  type: MapActionTypes.LOAD_SUCCESS,
  map: map,
});

const load = (mapId) => (dispatch) => {
  dispatch(loadStart());

  return GMapsApi.load()
    .then(() => {
      const map = new window.google.maps.Map(document.getElementById(mapId), {
        zoom: 1,
        center: { lat: 40.712600, lng: -74.006100 },
        zoomControl: true,
        mapTypeControl: false,
        scaleControl: true,
        streetViewControl: false,
        rotateControl: false,
        fullscreenControl: false,
      });
      var styledMapType = new window.google.maps.StyledMapType(MapStyles);
      map.mapTypes.set('styled_map', styledMapType);
      map.setMapTypeId('styled_map');
      return map;
    })
    .then((map) => {
      dispatch(loadSuccess(map));
    });
};

export default {
  load
};


