import Cluster from './Cluster';
import { getOverlayClass } from './OverlayViewProvider';

export default class Clusterer {
  constructor(map, markers) {

    this.map = map;
    this.markers = markers;
    this.clusters = [];
    this.ready = false; // keep?
    this.gridSize = 60;
    this.minClusterSize = 2;
    this.prevZoom = this.map.getZoom();

    window.google.maps.event.addListener(this.map, 'zoom_changed', () => {
      // Determines map type and prevent illegal zoom levels
      const maxZoom = this.map.mapTypes[this.map.getMapTypeId()].maxZoom;
      const zoom = Math.min(this.map.getZoom(), maxZoom);

      if (this.prevZoom !== zoom) {
        this.prevZoom = zoom;
        this.resetViewport();
      }
    });

    window.google.maps.event.addListener(this.map, 'idle', () => {
      this.redraw();
    });

    this.overlay = new (getOverlayClass())(map, this);
  }

  onAdd() {
    this.setReady(true);
  }

  fitMapToMarkers() {
    var bounds = new window.google.maps.LatLngBounds();
    this.markers.forEach((marker) => bounds.extend(marker.getPosition()));
    this.map.fitBounds(bounds);
  }

  setReady(ready) {
    if (!this.ready) {
      this.ready = ready;
      this.createClusters();
    }
  };

  getExtendedBounds = function (bounds) {
    const projection = this.overlay.getProjection();

    // Turn the bounds into latlng.
    const tr = new window.google.maps.LatLng(
      bounds.getNorthEast().lat(),
      bounds.getNorthEast().lng()
    );
    const bl = new window.google.maps.LatLng(
      bounds.getSouthWest().lat(),
      bounds.getSouthWest().lng()
    );

    // Convert the points to pixels and the extend out by the grid size.
    const trPix = projection.fromLatLngToDivPixel(tr);
    trPix.x += this.gridSize;
    trPix.y -= this.gridSize;

    const blPix = projection.fromLatLngToDivPixel(bl);
    blPix.x -= this.gridSize;
    blPix.y += this.gridSize;

    // Convert the pixel points back to LatLng
    const ne = projection.fromDivPixelToLatLng(trPix);
    const sw = projection.fromDivPixelToLatLng(blPix);

    // Extend the bounds to contain the new bounds.
    bounds.extend(ne);
    bounds.extend(sw);

    return bounds;
  };

  isMarkerInBounds(marker, bounds) {
    return bounds.contains(marker.getPosition());
  };

  clearMarkers() {
    this.resetViewport(true);

    // Set the markers a empty array.
    this.markers = [];
  };

  addMarkers(markers) {
    this.markers = this.markers.concat(markers);
    this.redraw()
  }

  resetViewport(hide) {
    // Remove all the clusters
    this.clusters.forEach((cluster) => cluster.remove());

    // Reset the markers to not be added and to be invisible.
    this.markers.forEach((marker) => {
      marker.isAdded = false;
      if (hide) {
        marker.setMap(null);
      }
    });

    this.clusters = [];
  }

  redraw() {
    this.createClusters();
  }

  distanceBetweenPoints(p1, p2) {
    if (!p1 || !p2) {
      return 0;
    }

    const R = 6371; // Radius of the Earth in km
    const dLat = (p2.lat() - p1.lat()) * Math.PI / 180;
    const dLon = (p2.lng() - p1.lng()) * Math.PI / 180;
    const a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(p1.lat() * Math.PI / 180) * Math.cos(p2.lat() * Math.PI / 180) *
      Math.sin(dLon / 2) * Math.sin(dLon / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    const d = R * c;

    return d;
  };

  addToClosestCluster(marker) {
    let distance = 40000; // Some large number
    let clusterToAddTo = null;
    const pos = marker.getPosition();
    this.clusters.forEach((cluster) => {
      const center = cluster.center;
      if (center) {
        const d = this.distanceBetweenPoints(center, pos);
        if (d < distance) {
          distance = d;
          clusterToAddTo = cluster;
        }
      }
    });

    if (clusterToAddTo && clusterToAddTo.isMarkerInClusterBounds(marker)) {
      clusterToAddTo.addMarker(marker);
    } else {
      const cluster = new Cluster(this);
      cluster.addMarker(marker);
      this.clusters.push(cluster);
    }
  }

  createClusters() {
    if (!this.ready) {
      return;
    }

    // Get our current map view bounds.
    // Create a new bounds object so we don't affect the map.
    const mapBounds = new window.google.maps.LatLngBounds(
      this.map.getBounds().getSouthWest(),
      this.map.getBounds().getNorthEast()
    );
    const bounds = this.getExtendedBounds(mapBounds);

    this.markers.forEach((marker) => {
      if (!marker.isAdded && this.isMarkerInBounds(marker, bounds)) {
        this.addToClosestCluster(marker);
      }
    });
  };
}









