import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Dialog from 'material-ui/Dialog';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import IconButton from 'material-ui/IconButton';
import CloseIcon from 'material-ui-icons/Close';
import InfoIcon from 'material-ui-icons/Info';
import { CircularProgress } from 'material-ui/Progress';
import Slide from 'material-ui/transitions/Slide';
import Typography from 'material-ui/Typography';
import Snackbar from 'material-ui/Snackbar';

import styles from './Viewer.css';
import ImageZoom from './ImageZoom';

class Viewer extends Component {

  static propTypes = {
    open: PropTypes.bool.isRequired,
    loading: PropTypes.bool.isRequired,
    photoInfo: PropTypes.object,
    image: PropTypes.object,
    onLoadImage: PropTypes.func.isRequired,
    onClose: PropTypes.func.isRequired,
  };

  constructor(props) {
    super(props);
    this.state = { showInfo: false };

  }

  handleOnOpening = () => {
    this.props.onLoadImage(this.props.photoInfo.id);
  }

  handleRequestClose = () => {
    this.props.onClose();
  }

  handleToggleInfo = (open) => () => {
    this.setState({ showInfo: open });
  }

  renderTitle() {
    const { open, loading, photoInfo } = this.props;

    const title = open && !loading && !!photoInfo
      ? photoInfo.description
      : '...';
    return (
      <Typography
        type="title"
        color="inherit"
        className={styles.title}
      >
        {title}
      </Typography>
    );
  }

  renderImage() {
    const { open, loading, image } = this.props;

    if (!open) {
      return;
    }

    if (loading || !image) {
      return (
        <div className={styles.loader}>
          <CircularProgress/>
        </div>
      );
    }

    return (
      <div className={styles.imageWrapper}>
        <ImageZoom image={image}/>
      </div>
    );
  }

  renderInfo() {
    const { open, loading, photoInfo } = this.props;

    if (!open || loading || !photoInfo) {
      return '';
    }

    return (
      <div>
        <p>{photoInfo.description}</p>
        <p>Year: {photoInfo.year}</p>
      </div>
    );
  }

  render() {
    const { open } = this.props;
    return (
      <Dialog
        fullScreen
        open={open}
        onEntering={this.handleOnOpening}
        onRequestClose={this.handleRequestClose}
        transition={<Slide direction="up"/>}
        classes={{ fullScreen: styles.dialogFullscreen }}
      >
        <AppBar classes={{ positionFixed: styles.appBar }}>
          <Toolbar>
            {this.renderTitle()}
            <IconButton
              color="contrast"
              aria-label="More Info"
              onClick={this.handleToggleInfo(!this.state.showInfo)}
            >
              <InfoIcon/>
            </IconButton>
            <IconButton
              color="contrast"
              onClick={this.handleRequestClose}
              aria-label="Close"
            >
              <CloseIcon/>
            </IconButton>
          </Toolbar>
        </AppBar>
        <div className={styles.content}>
          {this.renderImage()}
        </div>
        <Snackbar
          open={this.state.showInfo}
          onRequestClose={this.handleToggleInfo(false)}
          transition={<Slide direction='up' />}
          message={this.renderInfo()}
        />
      </Dialog>
    )
  }
}

export default Viewer;


