
const getImageMetadata = (image) => ({
  src: image.src,
  width: image.width,
  height: image.height
});


const loadImage = (imageId) => {
  const imageUrl = `https://invinciblecities.camden.rutgers.edu/files/${imageId}.jpg`;
  return new Promise((resolve, reject) => {
    const image = new Image();
    image.addEventListener('load', () => resolve(getImageMetadata(image)));
    image.addEventListener('error', reject);
    image.src = imageUrl;
  });
};

export default {
  loadImage
};


