import React, { Component } from 'react';
import PropTypes from 'prop-types';

import IconButton from 'material-ui/IconButton';
import AddCircle from 'material-ui-icons/AddCircle';
import RemoveCircle from 'material-ui-icons/RemoveCircle';

import styles from './ImageZoom.css';

class ImageZoom extends Component {
  static propTypes = {
    image: PropTypes.object,
  };

  constructor(props) {
    super(props);
    this.state = {
      loaded: false,
      zoom: 1,
    };
  }

  handleZoomUpdate = (increment) => () => {
    const oldZoom = this.state.zoom;

    const zoom = Math.max(Math.min(oldZoom + increment, 10), 1);

    if (oldZoom === zoom) {
      return;
    }

    this.setState({ zoom });
  }

  getImageStyles() {
    const { height, width } = this.props.image;

    const { clientHeight, clientWidth } = this.container;
    const { zoom } = this.state;

    const containerAspect = clientHeight / clientWidth;
    const imageAspect = height / width;
    if (containerAspect > imageAspect) {
      const zoomedHeight = clientWidth * imageAspect * zoom;
      const offset = (clientHeight - zoomedHeight) / 2;
      return {
        height: zoomedHeight,
        width: clientWidth * zoom,
        top: offset > 0 ? offset : 0,
        left: 0,
      };
    }

    const zoomedWidth = clientHeight / imageAspect * zoom;
    const offset = (clientWidth - zoomedWidth) / 2;

    return {
      height: clientHeight * zoom,
      width: zoomedWidth,
      top: 0,
      left: offset > 0 ? offset : 0,
    };
  }

  renderImage() {
    const { image } = this.props;

    return (
      <img
        src={image.src}
        alt="img"
        className={styles.img}
        style={this.getImageStyles()}
        onDoubleClick={this.handleZoomUpdate(1)}
      />
    );
  }

  render() {

    return (
      <div className={styles.fixed}>
        <div
          className={styles.container}
          ref={(container) => {
            this.container = container;
            if (!this.state.loaded) {
              this.setState({ loaded: true });
            }
          }}
        >
          {this.container && this.renderImage()}
        </div>
        <div className={styles.zoomButtons}>
          <IconButton onClick={this.handleZoomUpdate(1)}>
            <AddCircle/>
          </IconButton>
          <IconButton onClick={this.handleZoomUpdate(-1)}>
            <RemoveCircle/>
          </IconButton>
        </div>
      </div>
    );
  }
}

export default ImageZoom;


