import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { CircularProgress } from 'material-ui/Progress';
import Clusterer from './clustering/Clusterer';



import classNames from 'classnames';
import styles from './Map.css';

const MAP_CONTAINER_ID = 'mapRoot';

class Map extends Component {

  static propTypes = {
    loadingMap: PropTypes.bool.isRequired,
    loadingData: PropTypes.bool.isRequired,
    map: PropTypes.object,
    images: PropTypes.arrayOf(PropTypes.object),
    onLoadMap: PropTypes.func.isRequired,
    onLoadData: PropTypes.func.isRequired,
    onOpenImage: PropTypes.func.isRequired,
  };

  componentDidMount() {
    this.props.onLoadMap(MAP_CONTAINER_ID);
    this.props.onLoadData();
  }

  renderLoader() {
    const { loading } = this.props;

    if (!loading) {
      return;
    }

    return (
      <div className={styles.loader} >
        <CircularProgress/>
      </div>
    );
  }

  renderMap() {
    const { loadingMap } = this.props;
    const classes = classNames(styles.gmap, { [styles.hide]: loadingMap });
    return (
      <div id={MAP_CONTAINER_ID} className={classes}></div>
    );
  }

  renderData() {
    const { loadingMap, loadingData, map, images } = this.props;

    if (loadingMap || loadingData || !map || !images) {
      return;
    }
    const markers = images.map((image) => {
      const { lat, lng } = image;

      const marker = new window.google.maps.Marker({
        position: { lat, lng },
        icon: 'https://static1.squarespace.com/static/54c92a1de4b01578e1ac210f/t/59791452bf629a8a6ab5514f/1501107282605/camera.png'
      });

      marker.addListener('click', () => {
        this.props.onOpenImage(image);
      });

      return marker;
    });

    if (!this.clusterer) {
      this.clusterer = new Clusterer(map, markers);
      this.clusterer.fitMapToMarkers();
    } else {
      this.clusterer.clearMarkers();
      this.clusterer.addMarkers(markers);
    }
    return null;
  }

  render() {
    const { className } = this.props;
    return (
      <div className={className}>
        {this.renderLoader()}
        {this.renderMap()}
        {this.renderData()}
      </div>
    )
  }
}

export default Map;


