import { combineReducers } from 'redux';
import mapReducer from './Map/reducers/mapReducer';
import dataReducer from './Map/reducers/dataReducer';
import viewerReducer from './Viewer/reducers/viewerReducer';
import filterDialogReducer from './Menu/reducers/filterDialogReducer';

const reducers = combineReducers({
  map: mapReducer,
  data: dataReducer,
  viewer: viewerReducer,
  filterDialog: filterDialogReducer,
});

export default reducers;


