import MapActionsTypes from '../constants/MapActionTypes';

const defaultState = {
  loading: false,
  map: null,
};

const mapReducer = (state = defaultState, action) => {
  switch (action.type) {
  case MapActionsTypes.LOAD:
    return {
      ...state,
      loading: true,
    };
  case MapActionsTypes.LOAD_SUCCESS:
    return {
      ...state,
      loading: false,
      map: action.map,
    };
  default:
    return state;
  }
};

export default mapReducer;


