import ViewerActionTypes from '../constants/ViewerActionTypes';
import ImageLoader from '../api/ImageLoader';

const open = (photoInfo) => ({
  type: ViewerActionTypes.OPEN,
  photoInfo,
});

const close = () => ({
  type: ViewerActionTypes.CLOSE,
});

const loadStart = () => ({
  type: ViewerActionTypes.LOAD_IMAGE,
});

const loadSuccess = (image) => ({
  type: ViewerActionTypes.LOAD_IMAGE_SUCCESS,
  image,
});

const loadImage = (photoId) => (dispatch) => {
  dispatch(loadStart());

  ImageLoader.loadImage(photoId)
    .then((image) => {
      dispatch(loadSuccess(image))
    });
};

export default {
  open: (photoInfo) => (dispatch) => dispatch(open(photoInfo)),
  close: () => (dispatch) => dispatch(close()),
  loadImage
};


